import {
  Controller,
  Get,
  Post,
  Body,
  Patch,
  Put,
  Param,
  Delete,
  Query,
  Request,
  BadRequestException,
  UseGuards,
  UsePipes,
} from '@nestjs/common';
import {
  ApiTags,
  ApiOperation,
  ApiResponse,
  ApiQuery,
  ApiParam,
  ApiBody,
  ApiBearerAuth,
} from '@nestjs/swagger';
import { PaginationDto } from '../../../../common/dto/pagination.dto';
import { DynamicDataService } from './dynamic-data.service';
import { JwtAuthGuard } from '../../../auth/guards/jwt-auth.guard';
import { TierLimitsGuard } from '../../../../common/guards/tier-limits.guard';
import { ObjectIdValidationPipe } from '../../../../common/pipes/objectid-validation.pipe';
import {
  DYNAMIC_DATA_ROUTES,
  API_VERSION,
} from '../../../../common/constants/api-routes.constants';

@ApiTags('dynamic-data')
@Controller(DYNAMIC_DATA_ROUTES.BASE)
@UseGuards(JwtAuthGuard)
@UsePipes(ObjectIdValidationPipe)
@ApiBearerAuth()
export class DynamicDataController {
  constructor(private readonly dynamicDataService: DynamicDataService) {}

  @Post()
  @UseGuards(TierLimitsGuard)
  @ApiOperation({ summary: 'Create new document in dynamic collection' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiBody({
    description: 'Document data to create',
    schema: {
      type: 'object',
      example: {
        product_name: 'iPhone 15 Pro',
        sku: 'IPHONE-15-PRO',
        price: 999.99,
        category: 'electronics',
        description: 'Latest iPhone model',
        tags: ['new', 'bestseller'],
        in_stock: true,
      },
    },
  })
  @ApiResponse({ status: 201, description: 'Document created successfully' })
  @ApiResponse({ status: 400, description: 'Validation failed' })
  @ApiResponse({ status: 404, description: 'Collection schema not found' })
  @ApiResponse({ status: 403, description: 'Tier limit reached' })
  create(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Body() data: Record<string, any>,
    @Request() req,
  ) {
    const userId = req?.user?.userId || null;
    return this.dynamicDataService.create(
      collectionName,
      databaseId,
      data,
      userId,
    );
  }

  @Post(DYNAMIC_DATA_ROUTES.BULK)
  @UseGuards(TierLimitsGuard)
  @ApiOperation({ summary: 'Create multiple documents in dynamic collection' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiBody({
    description: 'Array of document data to create',
    schema: {
      type: 'array',
      items: {
        type: 'object',
      },
      example: [
        {
          product_name: 'iPhone 15 Pro',
          sku: 'IPHONE-15-PRO',
          price: 999.99,
          category: 'electronics',
          description: 'Latest iPhone model',
          tags: ['new', 'bestseller'],
          in_stock: true,
        },
        {
          product_name: 'Samsung Galaxy S24',
          sku: 'SAMSUNG-S24',
          price: 899.99,
          category: 'electronics',
          description: 'Latest Samsung flagship',
          tags: ['new', 'android'],
          in_stock: true,
        },
      ],
    },
  })
  @ApiResponse({
    status: 201,
    description: 'Documents created (returns success/failed count)',
  })
  @ApiResponse({ status: 400, description: 'Invalid input' })
  @ApiResponse({ status: 404, description: 'Collection schema not found' })
  @ApiResponse({ status: 403, description: 'Tier limit reached' })
  createMany(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Body() dataArray: Record<string, any>[],
    @Request() req,
  ) {
    const userId = req?.user?.userId || null;

    if (!Array.isArray(dataArray)) {
      throw new BadRequestException('Body must be an array of objects');
    }

    return this.dynamicDataService.createMany(
      collectionName,
      databaseId,
      dataArray,
      userId,
    );
  }

  @Put(DYNAMIC_DATA_ROUTES.REPLACE_ALL)
  @UseGuards(TierLimitsGuard)
  @ApiOperation({
    summary: 'Replace all documents in collection with new data array',
  })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiBody({
    description:
      'Array of new document data (will delete all old data and create new)',
    schema: {
      type: 'array',
      items: {
        type: 'object',
      },
      example: [
        {
          product_name: 'New Product 1',
          sku: 'NEW-001',
          price: 499.99,
          category: 'electronics',
          in_stock: true,
        },
        {
          product_name: 'New Product 2',
          sku: 'NEW-002',
          price: 599.99,
          category: 'electronics',
          in_stock: true,
        },
      ],
    },
  })
  @ApiResponse({
    status: 200,
    description:
      'All old data deleted and new data created (returns deleted/created count)',
  })
  @ApiResponse({ status: 400, description: 'Invalid input' })
  @ApiResponse({ status: 404, description: 'Collection schema not found' })
  @ApiResponse({ status: 403, description: 'Tier limit reached' })
  replaceAll(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Body() dataArray: Record<string, any>[],
    @Request() req,
  ) {
    const userId = req?.user?.userId || null;

    if (!Array.isArray(dataArray)) {
      throw new BadRequestException('Body must be an array of objects');
    }

    return this.dynamicDataService.replaceAll(
      collectionName,
      databaseId,
      dataArray,
      userId,
    );
  }

  @Get()
  @ApiOperation({ summary: 'Get all documents from dynamic collection' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiQuery({ name: 'page', required: false, type: Number, example: 1 })
  @ApiQuery({ name: 'limit', required: false, type: Number, example: 10 })
  @ApiQuery({ name: 'search', required: false, type: String })
  @ApiQuery({
    name: 'populate',
    required: false,
    type: Boolean,
    description: 'Auto-populate reference fields',
  })
  @ApiQuery({
    name: 'populateDepth',
    required: false,
    type: Number,
    description: 'Depth of nested population (default: 1)',
  })
  @ApiResponse({ status: 200, description: 'Documents retrieved' })
  findAll(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Query() paginationDto: PaginationDto,
    @Query('populate') populate?: boolean,
    @Query('populateDepth') populateDepth?: number,
    @Request() req?,
  ) {
    const userId = req?.user?.userId || null;
    return this.dynamicDataService.findAll(
      collectionName,
      userId,
      databaseId,
      paginationDto,
      undefined,
      { populate, populateDepth },
    );
  }

  @Post(DYNAMIC_DATA_ROUTES.QUERY)
  @ApiOperation({ summary: 'Query documents with custom filter' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiBody({
    description: 'Query filter with optional sort, limit, skip',
    schema: {
      type: 'object',
      properties: {
        filter: {
          type: 'object',
          example: { category: 'electronics', in_stock: true },
        },
        sort: {
          type: 'object',
          example: { price: -1, createdAt: -1 },
        },
        limit: { type: 'number', example: 20 },
        skip: { type: 'number', example: 0 },
      },
    },
  })
  @ApiResponse({ status: 200, description: 'Query results returned' })
  query(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Body()
    body: {
      filter: Record<string, any>;
      sort?: Record<string, 1 | -1>;
      limit?: number;
      skip?: number;
    },
  ) {
    return this.dynamicDataService.query(collectionName, body.filter, {
      sort: body.sort,
      limit: body.limit,
      skip: body.skip,
    });
  }

  @Get(DYNAMIC_DATA_ROUTES.COUNT)
  @ApiOperation({ summary: 'Count documents in collection' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiResponse({ status: 200, description: 'Count returned' })
  count(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
  ) {
    return this.dynamicDataService.count(collectionName);
  }

  @Get(DYNAMIC_DATA_ROUTES.BY_ID)
  @ApiOperation({ summary: 'Get document by ID' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiParam({ name: 'id', example: '507f1f77bcf86cd799439011' })
  @ApiQuery({
    name: 'populate',
    required: false,
    type: Boolean,
    description: 'Auto-populate reference fields',
  })
  @ApiQuery({
    name: 'populateDepth',
    required: false,
    type: Number,
    description: 'Depth of nested population (default: 1)',
  })
  @ApiResponse({ status: 200, description: 'Document found' })
  @ApiResponse({ status: 404, description: 'Document not found' })
  findOne(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Param('id') id: string,
    @Query('populate') populate?: boolean,
    @Query('populateDepth') populateDepth?: number,
    @Request() req?,
  ) {
    const userId = req?.user?.userId || null;
    return this.dynamicDataService.findById(
      collectionName,
      id,
      userId,
      databaseId,
      { populate, populateDepth },
    );
  }

  @Patch(DYNAMIC_DATA_ROUTES.BY_ID)
  @ApiOperation({ summary: 'Update document by ID' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiParam({ name: 'id', example: '507f1f77bcf86cd799439011' })
  @ApiBody({
    description: 'Fields to update (partial update supported)',
    schema: {
      type: 'object',
      example: {
        price: 899.99,
        in_stock: false,
      },
    },
  })
  @ApiResponse({ status: 200, description: 'Document updated' })
  @ApiResponse({ status: 400, description: 'Validation failed' })
  @ApiResponse({ status: 404, description: 'Document not found' })
  update(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Param('id') id: string,
    @Body() data: Record<string, any>,
    @Request() req,
  ) {
    const userId = req?.user?.userId || null;
    return this.dynamicDataService.update(
      collectionName,
      id,
      databaseId,
      data,
      userId,
    );
  }

  @Put(DYNAMIC_DATA_ROUTES.BY_ID)
  @ApiOperation({ summary: 'Replace document by ID (full replacement)' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiParam({ name: 'id', example: '507f1f77bcf86cd799439011' })
  @ApiBody({
    description: 'Complete document data (replaces entire document)',
    schema: {
      type: 'object',
      example: {
        product_name: 'iPhone 15 Pro Max',
        sku: 'IPHONE-15-PRO-MAX',
        price: 1199.99,
        category: 'electronics',
        description: 'Latest iPhone model with larger screen',
        tags: ['new', 'bestseller', 'premium'],
        in_stock: true,
      },
    },
  })
  @ApiResponse({ status: 200, description: 'Document replaced' })
  @ApiResponse({ status: 400, description: 'Validation failed' })
  @ApiResponse({ status: 404, description: 'Document not found' })
  replace(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Param('id') id: string,
    @Body() data: Record<string, any>,
    @Request() req,
  ) {
    const userId = req?.user?.userId || null;
    return this.dynamicDataService.replace(
      collectionName,
      id,
      databaseId,
      data,
      userId,
    );
  }

  @Delete(DYNAMIC_DATA_ROUTES.BY_ID)
  @ApiOperation({ summary: 'Soft delete document by ID' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiParam({ name: 'id', example: '507f1f77bcf86cd799439011' })
  @ApiResponse({ status: 200, description: 'Document deleted' })
  @ApiResponse({ status: 404, description: 'Document not found' })
  softDelete(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Param('id') id: string,
    @Request() req,
  ) {
    const userId = req?.user?.userId || null;
    return this.dynamicDataService.softDelete(
      collectionName,
      id,
      userId,
      databaseId,
    );
  }

  @Delete(DYNAMIC_DATA_ROUTES.HARD_DELETE)
  @ApiOperation({ summary: 'Permanently delete document by ID' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiParam({ name: 'id', example: '507f1f77bcf86cd799439011' })
  @ApiResponse({ status: 200, description: 'Document permanently deleted' })
  @ApiResponse({ status: 404, description: 'Document not found' })
  hardDelete(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Param('id') id: string,
    @Request() req,
  ) {
    const userId = req?.user?.userId || null;
    return this.dynamicDataService.hardDelete(
      collectionName,
      id,
      userId,
      databaseId,
    );
  }

  @Post(DYNAMIC_DATA_ROUTES.RESTORE)
  @ApiOperation({ summary: 'Restore soft-deleted document' })
  @ApiParam({ name: 'databaseId', example: '507f1f77bcf86cd799439011' })
  @ApiParam({ name: 'collectionName', example: 'products' })
  @ApiParam({ name: 'id', example: '507f1f77bcf86cd799439011' })
  @ApiResponse({ status: 200, description: 'Document restored' })
  @ApiResponse({ status: 404, description: 'Document not found' })
  restore(
    @Param('databaseId') databaseId: string,
    @Param('collectionName') collectionName: string,
    @Param('id') id: string,
    @Request() req,
  ) {
    const userId = req?.user?.userId || null;
    return this.dynamicDataService.restore(
      collectionName,
      id,
      userId,
      databaseId,
    );
  }
}
